<template>
  <div class="ui three item menu">
    <!-- <a
      class="item"
      @click="toPage(3)"
      :class="$store.state.currenIndex == 3 ? 'active' : ''"
    >
      Carteleras
    </a> -->
    <a
      class="item"
      @click="toPage(0)"
      :class="$store.state.currenIndex == 0 ? 'active' : ''"
    >
      Tandas
    </a>
    <a
      class="item"
      @click="toPage(1)"
      :class="$store.state.currenIndex == 1 ? 'active' : ''"
    >
      Proximamente
    </a>
    <a
      class="item"
      @click="toPage(2)"
      :class="$store.state.currenIndex == 2 ? 'active' : ''"
    >
      Promociones
    </a>
  </div>
</template>

<script>
export default {
  name: "barra",
  methods: {
    toPage(index) {
      if (this.$store.state.currenIndex != index) {
        if (index == 0) this.$router.push("/tandas");
        if (index == 1) this.$router.push("/proximamente");
        if (index == 2) this.$router.push("/promociones");
        // if (index == 3) this.$router.push("/carteleras");
      }
    },
  },
};
</script>